import Scrollbar from 'smooth-scrollbar';

let scrollbar;

const scrollbarInit = ScrollTrigger => {
	const ScrollContainer = document.querySelector('#my-scrollbar');
	const ScrollbarOptions = {
		damping: 0.1, // ë‚®ì„ìˆ˜ë¡ ë§Žì´ ë¯¸ë„ëŸ¬ì§ ë³´í†µ 0.1 ~ 0.5 ì‚¬ì´ë¥¼ ë§Žì´ ë„£ìŒ
		// thumbMinSize: 0,
		// renderByPixels: true,
		alwaysShowTracks: false, // true: scroll barê°€ í•­ìƒ ë³´ì´ê²Œ í•œë‹¤.
		// continuousScrolling: true,
		// wheelEventTarget: ,
		// plugins: {},
	};
	scrollbar = Scrollbar.init(ScrollContainer, { ...ScrollbarOptions });
	Scrollbar.init(ScrollContainer, ScrollbarOptions);
	ScrollTrigger.scrollerProxy(ScrollContainer, {
		scrollTop(value) {
			if (arguments.length) {
				scrollbar.scrollTop = value; // setter
			}
			return scrollbar.scrollTop; // getter
		},
	});

	scrollbar.addListener(ScrollTrigger.update);
	ScrollTrigger.defaults({ scroller: ScrollContainer });
};

const markers = gsap => {
	if (document.querySelector('.gsap-marker-scroller-start')) {
		const markers = gsap.utils.toArray('[class *= "gsap-marker"]');
		scrollbar.addListener(({ offset }) => {
			gsap.set(markers, { marginTop: -offset.y });
		});
	}
};

